import React from "react";

import useMenu from "hooks/useMenu";
import useSearch from "hooks/useSearch";
import useGetList from "hooks/useGetList";

import ChartSearch from "components/search/ChartSearch";
import Charts from "features/chart/components/Charts";
import MessageModal from "common/modal/MessageModal";

export default function Chart() {
    const menu = useMenu({ page: "Chart", menu: "chart", menuTitle: "", menuNum: 8 }); // / 

    // line chart  
    const {
        params,
        isLoading: lineLoading,
        data: lineChartData
    } = useGetList({
        menu,
        url: "/web/line-chart"
    });

    // pie chart  
    const { isLoading: pieLoading, data: pieChartData } = useGetList({
        menu,
        url: "/web/pie-chart"
    });

    const handleSearch = useSearch({ params }); // 

    return (
        <>
            <ChartSearch heading=" " menu={menu} handleSearch={handleSearch} />
            <Charts lineLoading={lineLoading} lineChartData={lineChartData} pieLoading={pieLoading} pieChartData={pieChartData} />
            <MessageModal />
        </>
    );
}
