import React, { useEffect } from "react";
import useMenu from "hooks/useMenu";
import useMessage from "hooks/useMessage";

import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";

import useStyles from "styles/customize/table/DetailTableStyles";
import theme from "styles/theme/form";
import { ThemeProvider } from "@mui/material/styles";
import Grid from "@mui/material/Grid";

import Input from "common/form/Input";
import ErrorMessage from "common/form/ErrorMessage";
import SubmitButton from "common/button/SubmitButton";
import MessageModal from "common/modal/MessageModal";

import { schema } from "components/Data";

export default function ChangeInfo() {
    const classes = useStyles();
    const menu = useMenu({ page: "ChangeInfo", menu: null, menuTitle: " ", menuNum: null });

    const { errors, clearErrors, reset, control, register, handleSubmit } = useForm({
        resolver: yupResolver(schema[menu])
    });

    const handleMessage = useMessage();

    useEffect(() => {
        clearErrors();
        reset({
            name: "",
            email: "",
            phone: ""
        });
    }, [clearErrors, reset]);

    //  
    const onSubmit = (data) => {
        console.log("data", data);
        handleMessage({ type: "message", message: "." });
    };

    return (
        <ThemeProvider theme={theme}>
            <form className={classes.form} onSubmit={handleSubmit(onSubmit)} noValidate autoComplete="off">
                <table className={classes.table}>
                    <colgroup>
                        <col width="12%" />
                        <col width="88%" />
                    </colgroup>
                    <tbody>
                        <tr>
                            <th></th>
                            <td>
                                <Input name="name" defaultValue="" inputType="text" control={control} />
                                {errors.name && <ErrorMessage text=" ." />}
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td>
                                <Input name="email" defaultValue="" inputType="text" control={control} />
                                {errors.email && <ErrorMessage text=" ." />}
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td>
                                <Input name="phone" defaultValue="" inputType="phone" control={control} />
                                {errors.phone && <ErrorMessage text=" ." />}
                            </td>
                        </tr>
                        <tr>
                            <th></th>
                            <td>
                                <input name="image" type="file" ref={register} />
                                {errors.image && <ErrorMessage text=" ." />}
                            </td>
                        </tr>
                    </tbody>
                </table>
                <Grid container justifyContent="flex-end" alignItems="center">
                    <SubmitButton type="submit" text=" " />
                </Grid>
            </form>
            <MessageModal />
        </ThemeProvider>
    );
}
