import { useState, useEffect, useCallback } from "react";
import { useLocation } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { searchSelector, setSearchFilters } from "slices/searchSlice";
import { isEmpty, queryToObject } from "utils/common";

const useSearchParams = (searchParams, chartSearchParams) => {
    const dispatch = useDispatch();
    const location = useLocation();
    const searchStates = useSelector(searchSelector);

    const [newParams, setNewParams] = useState(null);

    const handleSearchParams = useCallback(() => {
        let params = {}; // API  params
        let newObject = {}; // redux store  
        const search = queryToObject(location.search);

        //   /      location.search redux store   
        // 1. location.search  params redux store  search state 
        // 2.  redux store  search state location.search 
        // 3. dispatch   
        Object.keys(searchParams).forEach((key) => {
            const reduxStateName = searchParams[key]; // searchParams[key] : searchSlice  search state key
            const currentReduxState = searchStates[reduxStateName]; // searchStates[searchParams[key]] : redux store  search state value
            const currentSearchParam = search[key] || search[chartSearchParams[reduxStateName]]; // search[key] || search[chartSearchParams[reduxStateName]] : location.search 

            // location.search  
            if (!isEmpty(currentSearchParam)) {
                // int   
                if (key === "pageNumber" || key === "pageShow") {
                    params[key] = parseInt(currentSearchParam);

                    if (parseInt(currentReduxState) !== parseInt(currentSearchParam)) {
                        Object.assign(newObject, { [reduxStateName]: parseInt(currentSearchParam) });
                    }
                } else if (currentReduxState !== currentSearchParam) {
                    // string 
                    params[key] = currentSearchParam;
                    Object.assign(newObject, { [reduxStateName]: currentSearchParam });
                } else {
                    // location.search redux state  
                    params[key] = currentReduxState || null;
                }
            } else {
                // location.search  
                params[key] = currentReduxState || null;
            }
        });

        // API  page  0 
        // location.search page  1 
        if (params?.pageNumber) {
            params = { ...params, pageNumber: params.pageNumber > 1 ? params.pageNumber - 1 : 0 };
        }

        // redux store  dispatch  
        if (Object.keys(newObject).length > 0) {
            dispatch(setSearchFilters(newObject));
        }

        setNewParams(params);
    }, [dispatch, searchParams, chartSearchParams, searchStates, location]);

    useEffect(() => {
        handleSearchParams();
    }, [handleSearchParams]);

    return newParams;
};

export default useSearchParams;
